import React from "react";
import { Element } from "react-scroll";
import {IconButton} from '@material-ui/core'
import { Facebook, Instagram, LinkedIn } from "@material-ui/icons";
import "./Contact.css"

const Contact = () => {
    return (
        <Element className="contact_us" id="Contact">
<h1> Contact  </h1>

        
        <div className="Contact__Container">
            <p>
                E-mail :<span>winorganicproducts@gmail.com</span>
            </p>
            <p>
                Github Username : <span>@kiruthikadev</span>
            </p>
            <div className="Contact__icon">
                <a href="google.com">
                    <IconButton>
                        <LinkedIn />
                    </IconButton>
                    </a>
                    <a href="google.com">
                    <IconButton>
                        <Facebook />
                    </IconButton>
                    </a>
                    <a href="google.com">
                    <IconButton>
                        <Instagram />
                    </IconButton>
                    </a>
                    </div>
        </div>
        </Element>
    )
}

export default Contact
